/* ------------------------------------------------------------------------
	                 				MULTIPLE CHOICE
---------------------------------------------------------------------------

	B2. Content Container

		C1. Question
			C1-multi:  			Multiple Choice

		C2. Answers Container

			D1. <li> Tag for Single and Multiple Choice

				E2. <label> for Multiple Choice 		(label._pi-control-checkbox)
					F3. Hidden <input> Form Element 		(input._pi_checkbox)
					F4. Fake Checkbox <div> 				(div._pi-control-indicator)
					F5. Fake Checkmark <div> 				(div._pi-control-indicator:after)

			D2. Free Text Question Field Container
				E3. Multiple Choice Selection Count

			D3. Multiple Choice Submit Button Container
				E4. Multiple Choice Submit Button

------------------------------------------------------------------------ */



// ================== C2. ANSWERS CONTAINER | MULTIPLE CHOICE ====================================
// --- Desktop and Tablet
div[data-question-type="multiple_choices_question"] ul._pi_answers_container {
	width: 100% !important;
}

// --- Mobile
#_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="multiple_choices_question"] ul._pi_answers_container {
	width: 98% !important;
}



	// ================== D1. <li> TAG FOR MULTIPLE CHOICE ====================================



		// ================== E2. <label> FOR MULTIPLE CHOICE ====================================
		// --- Desktop
		ul._pi_answers_container li label._pi-control-checkbox {
			background-color: #fff !important;
			color: #0272b6 !important;
			border: 1px solid #0272b6;
			border-radius: 4px;
			padding: 15px 15px 15px 0 !important;
			// background-color: lightgreen !important;
		}

		// --- Desktop Only Hover
		@media (min-width:769px) {
			ul._pi_answers_container li:hover label._pi-control-checkbox {
				background-color: #0272b6 !important;

				border: 1px solid #0272b6;
				color: #fff !important;
			}
		}

		// --- Mobile
		#_pi_surveyWidgetContainer.mobile-enabled label._pi-control._pi-control-checkbox {
			font-size: 15px !important;
			padding: 10px 10px 10px 0 !important;
		}



			// ================== F3. HIDDEN <input> FORM ELEMENT ====================================



			// ================== F4. FAKE CHECKBOX <div> ====================================
			// --- All Devices
			div._pi-control-indicator {
				background-color: #fff !important;
				border: 1px solid #868f96 !important;
				border-radius: 2px !important;
				border-width: 1px !important;
				margin-top: 0px !important;
			}

			// --- Hover on Desktop
			@media (min-width:769px) {
				ul._pi_answers_container li:hover div._pi-control-indicator {
					background-color: rgba(255, 255, 255, 1) !important;
					border: 1px solid #fff !important;
				}
			}






			// ================== F5. FAKE CHECKMARK <div> ====================================
			// --- All Devices
			div._pi-control-indicator:after {
				border-color: #0272b6 !important;
			}

			// --- Hover on Desktop
			@media (min-width:769px) {
				ul._pi_answers_container li:hover div._pi-control-indicator:after {
					border-color: #0272b6 !important;
				}
			}






	// ================== D2. FREE TEXT QUESTION FIELD CONTAINER ====================================
	div[data-question-type="multiple_choices_question"] ._pi_free_text_question_field_container {
		background-color: rgba(255, 255, 255, 0);
		margin: 0 !important;
	}






		// ================== E3. MULTIPLE CHOICE SELECTION COUNT ====================================
		div._pi_multiple_choices_count {
			height: auto;
			padding: 4px 0 6px !important;
			text-align: center;
			// background-color: ivory !important;
		}






	// ================== D3. MULTIPLE CHOICE SUBMIT BUTTON CONTAINER ====================================
	div[data-question-type="multiple_choices_question"] ._pi_multiple_choices_question_submit_button_container {
		background-color: rgba(255, 255, 255, 0);
		margin: 0 !important;
	}



