/* Text */

ul._pi_answers_container li label._pi-control-checkbox {
  /* 			width: initial; */
  /* 			float: initial; */
  /* 			margin: initial; */
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 100%;
  position: static;
  overflow: hidden;
  margin: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  padding-top: 8px;
  /* Match to single-choice */
  padding-bottom: 8px;
  /* Match to single-choice */
  padding-left: 0;
  /* Do NOT make it a variable. There's a margin already in checkbox  */
  padding-right: 15px;
  /* Match it to single-choice label right & left padding */
}

/* Hidden Checkbox Form Element (nothing) */

/* Simulated Checkbox */

._pi-control-indicator {
  position: static;
  display: block;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 14px;
  margin-top: 2px;
  /* CREATE a variable for vertical adjust of the box */
  margin-bottom: 0;
  margin-left: 10px;
  /* Make sure it matches the variable for radio left margin */
  margin-right: 10px;
  /* Make sure it matches the variable for radio left margin */
  width: 14px;
  height: 14px;
  border: 1px solid #000;
}

/* Simulated Checkmark (nothing) */

._pi-control-checkbox ._pi-control-indicator:after {
  left: 4px;
  width: 4px;
  height: 10px;
  border: solid #000;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

._pi-control-indicator:after {
  content: '';
  position: relative;
  display: none;
}

