/* ================== A. SURVEY WIDGET ================== */
	// Default
	div#_pi_surveyWidget {
		height: 				auto;
		width: 				100%;
		margin: 				auto;
		padding-bottom: 	0px;
		z-index: 			50;
	
		display: 			flex;
      flex-direction: 	column;
      flex-wrap:			nowrap;
      justify-content: 	flex-start;
      align-items: 		center;

		border-radius: 0;
		// background-color: #fff !important;
		background-color: $bg-color-widget;
	}

	// Fullscreen
	div#_pi_surveyWidgetCustom {
		display: 			flex;
		flex-direction: 	column;
		flex-wrap: 			nowrap;
		justify-content: 	space-between;
		align-items: 		center;
		align-content: 	space-between;			
	}

	#_pi_surveyWidget[survey-widget-type="inlinesurvey"],
	#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="inlinesurvey"] {
		border: 1px solid #dde2e6 !important;
		z-index: 5 !important;
		border-radius: 3px;
	}

	div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"],
	div#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="bottombarsurvey"] {
		border-top: 2px solid #44484C !important;
	}









// ------------------------------------------------------------------------
//            							1. INLINE
// ------------------------------------------------------------------------
	#_pi_surveyWidget[survey-widget-type="inlinesurvey"],
	#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="inlinesurvey"] {
		margin: 			10px 0 20px !important;
		// border-top: 	2px solid red !important;
		// background-color: yellow;
	}

	#_pi_surveyWidget[survey-widget-type="inlinesurvey"] ul._pi_answers_container {
		min-width: 		320px !important;
	}	






// ------------------------------------------------------------------------
//            						  2. BOTTOM BAR
// ------------------------------------------------------------------------
	#_pi_surveyWidget[survey-widget-type="bottombarsurvey"],
	#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="bottombarsurvey"] {
		position: 			fixed !important;
		bottom: 				0;
		left: 				0;
		right: 				0;
		margin: 				0 !important;
		padding: 			0;
		// border: 				0 !important;
		// border-top: 		1px solid #999 !important;
		box-shadow: 		0px -1px 10px rgba(0,0,0,0.2);
		z-index: 			18000;
		// background-color: #fff;
	}






// ------------------------------------------------------------------------
//            						3. DOCKED WIDGET
// ------------------------------------------------------------------------
	#_pi_surveyWidget[survey-widget-type="dockedwidgetsurvey"],
	#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="dockedwidgetsurvey"] {
		position: 		fixed;
		// right: 			5%;
		bottom: 			0;
		max-height: 	0;
		height: 			auto;
		width: 			300px;
		z-index: 		18000;
		overflow: 		hidden;
		// background-color: red !important;
	}

	#_pi_surveyWidget[survey-widget-type="dockedwidgetsurvey"] {
		border-width: 						1px;
		border-style: 						solid;
		border-color: 						#dcdcdb;
		border-top-left-radius: 		0px;
		border-top-right-radius: 		0px;
		border-bottom-left-radius: 	0px;
		border-bottom-right-radius: 	0px;
		border-bottom: 					0px solid transparent !important;
	}

	#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="dockedwidgetsurvey"] {
		width: 			90%;
		position: 		relative;
		top: 				auto;
		left: 			auto;
		right: 			auto;
		bottom: 			auto;
		margin-top: 	20%;
		margin-left: 	auto;
		margin-right: 	auto;
		border: 			2px solid rgba(174, 174, 174, 0.6) !important;
		border-radius: 2px;
		// background-color: red !important;
	}





// ------------------------------------------------------------------------
//            					4. FULLSCREEN WIDGET
// ------------------------------------------------------------------------
	#_pi_surveyWidget[survey-widget-type="fullscreensurvey"] {
		// background-color: red !important;
	}

	#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="fullscreensurvey"] {
		// background-color: yellow !important;
	}




// ------------------------------------------------------------------------
//            						5. TOP BAR
// ------------------------------------------------------------------------
	#_pi_surveyWidget[survey-widget-type="topbarsurvey"] {
		position: 		fixed;
		top: 				0px;
		left: 			0;
		right: 			0;
		max-height: 	0;
		height: 			auto;
		width: 			100%;
		z-index: 		18000;
		overflow: 		hidden;
		// background-color: pink !important;
	}

	#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="topbarsurvey"] {
		width: 			90%;
		position: 		relative;
		top: 				auto;
		left: 			auto;
		right: 			auto;
		bottom: 			auto;
		margin-top: 	10%;
		margin-left: 	auto;
		margin-right: 	auto;
		border: 			3px solid rgba(174, 174, 174, 0.6);;
		border-radius: 5px;
		// background-color: yellow !important;
	}




